var categoryNames = ["apple", "banana", "orange", "broccoli", "carrot"];

var apple, banana, carrot, broccoli, orange = false;

var x = document.getElementById("myDIV");
x.style.display = "none";

import * as tf from '@tensorflow/tfjs';
import yolo, { downloadModel } from 'tfjs-yolo-tiny';

import { Webcam } from './webcam';

let model;
const webcam = new Webcam(document.getElementById('webcam'));

(async function main() {
    try {
        ga();
        model = await downloadModel();

        // alert("Just a heads up! We'll ask to access your webcam so that we can " +
        //   "detect objects in semi-real-time. \n\nDon't worry, we aren't sending " +
        //   "any of your images to a remote server, all the ML is being done " +
        //   "locally on device, and you can check out our source code on Github.");

        await webcam.setup();

        doneLoading();
        run();
    } catch(e) {
        console.error(e);
        showError();
    }
})();

async function run() {
    while (true) {
        const inputImage = webcam.capture();

        const t0 = performance.now();

        const boxes = await yolo(inputImage, model);
        //const boxes = await yolo(inputImage, model, { classProbThreshold: 0.2 });

        inputImage.dispose();

        const t1 = performance.now();
        //console.log("YOLO inference took " + (t1 - t0) + " milliseconds.");

        //console.log('tf.memory(): ', tf.memory());

        clearRects();
        
        //Set all objects to false;
        apple = false;
        banana = false;
        carrot = false;
        broccoli = false;
        orange = false;
        
        boxes.forEach(box => {
            const {
                top, left, bottom, right, classProb, className,
            } = box;

            console.log(className);

            for (var i = 0; i<categoryNames.length; i++) {
                if (className === categoryNames[i]) {
                    //sounds[i].play();
                    drawRect(left, top, right-left, bottom-top, `${className} Confidence: ${Math.round(classProb * 100)}%`)
                }


                //Check for specific fruits & vegs
                if (className === "apple") {
                    apple = true; 
                } else if (className === "banana") {
                    banana = true; 
                } else if (className === "broccoli") {
                    broccoli = true; 
                } else if (className === "carrot") {
                    carrot = true; 
                } else if (className === "orange") {
                    orange = true; 
                }



                //Specific combinations trig specific images
                if(broccoli && orange) {
                    console.log("Broccoli+orange salad");
                    //image(broccoli_orange_img, 0,0,width, height);
                } else if (carrot && apple) {
                    console.log("Carrot + apple salad");
                    //image(carrot_apple_img, 0,0,width, height);

                } 
                
                if (apple && orange) {
                    console.log("Banana, apple, orange smoothie");
                    //image(banana_apple_orange_img, 0,0,width, height);
                    x.style.display = "block"; 
                } else {
                    x.style.display = "none"; 
                }
            }     
        });

        await tf.nextFrame();
    }
}

const webcamElem = document.getElementById('webcam-wrapper');

function drawRect(x, y, w, h, text = '', color = 'red') {
    const rect = document.createElement('div');
    rect.classList.add('rect');
    rect.style.cssText = `top: ${y}; left: ${x}; width: ${w}; height: ${h}; border-color: ${color}`;

    const label = document.createElement('div');
    label.classList.add('label');
    label.innerText = text;
    rect.appendChild(label);

    webcamElem.appendChild(rect);
}

function clearRects() {
    const rects = document.getElementsByClassName('rect');
    while(rects[0]) {
        rects[0].parentNode.removeChild(rects[0]);
    }
}

function doneLoading() {
    const elem = document.getElementById('loading-message');
    elem.style.display = 'none';

    const successElem = document.getElementById('success-message');
    successElem.style.display = 'block';

    const webcamElem = document.getElementById('webcam-wrapper');
    webcamElem.style.display = 'flex';
}

function showError() {
    const elem = document.getElementById('error-message');
    elem.style.display = 'block';
    doneLoading();
}

function ga() {
    if (process.env.UA) {
        window.dataLayer = window.dataLayer || [];
        function gtag(){dataLayer.push(arguments);}
        gtag('js', new Date());
        gtag('config', process.env.UA);
    }
}








